<script>
import {OKX_CONNECT_ERROR_CODES,OKXTonConnect,
  OKX_TON_CONNECTION_AND_TRANSACTION_EVENT} from "@okxconnect/tonsdk"
import { OKXTonConnectUI, THEME } from "@okxconnect/ui";


import QrcodeVue from 'qrcode.vue'
var selectedReturnStrategy = "none";
var selectedDeepLink = "tg://resolve";
var selectedLanguage = "en_US";
var selectedTheme = THEME.LIGHT;


export default {
  name: 'App',
  components: {
    QrcodeVue
  },
  data: ()=> {
    return {
      testTopic: '',
      testPublicKey: '',
      testProofPayload: '',
      pendingDeepLink: '-',
      logList: [],
      connectReady: false,
      connectStatusStr: '连接钱包',
      disconnectStatusStr: '断开钱包',
      localPubKey: '',
      localSecKey: '',
      testTransaction:'',
      testTransactionStr:'发送交易',
      walletAddress:'',
      testboc:'',
      ProofPayloadVerify:'',
      showModal: false,
      showVerifyRow: false,
      qrValue: 'https://www.okx.com/download',
      currentStatus:"",
      language: selectedLanguage,
      theme: selectedTheme,
      THEME: THEME,
      withUI: localStorage.getItem("autoRefresh") == "true",
      returnStrategies: ["none", "back", "deeplink"],
      returnStrategy: selectedReturnStrategy,
      deeplink: selectedDeepLink,
      handleWithUI: (isChecked) => {
        localStorage.setItem("autoRefresh", `${isChecked}`);
        location.reload();
      },
      // isChecked:localStorage.getItem("autoRefresh") == "true",
      // handleChange:(isChecked) => {
      //   localStorage.setItem("autoRefresh",`${isChecked}`)
      // }
    }
  },
  computed: {
    getWalletAddress:function (){
      return this.walletAddress;
    },


  },
  methods: {
    async connectBtnClicked() {
      console.log(`encryptStoreMsg   connectBtnClicked  ${Date.now()}`);
        this.testTonSdk();
    },

    async disconnectAction() {
      try {
        console.log('get disconnect wallet btn clicked');
        await window.provider.disconnect();
      } catch (error) {
        console.log('Disconnect  error ==>> ',error.message);
        this.currentStatus = error.message;
      }
    },
    async walletInit() {
      var TON;
      if (!this.withUI) {
        console.log('---init wallet---');
        TON = new OKXTonConnect({
          metaData:{
            name:"OKX TON Connect Demo",
            icon: "https://avatars.githubusercontent.com/u/37784886"
          }})
      } else {
        console.log('---init wallet UI--');
        TON = new OKXTonConnectUI({
          dappMetaData: {
            name: "OKX TON Connect Demo",
            icon: "https://avatars.githubusercontent.com/u/37784886",
          },
          actionsConfiguration: {
            returnStrategy: 'tg://resolve',
            tmaReturnUrl:'back',
            modals: "all",
          },
          uiPreferences: {
            theme: THEME.LIGHT
          },
          buttonRootId: 'button-root',
          language: 'en_US',
        });
      }
      window.tonTest = TON;
    },

    async resotreConnectAction() {
      console.log('get restore wallet btn clicked');
      window.provider.restoreConnection();
    },
    // clearCacheKeyPair: () => {
    //   console.log(`generate local key pair`)
    //   testCM.cleanCache()
    //   // testTonSdk()
    //
    // },

    async mockSwap(){
      var data =  {"messages":[{"address":"EQARULUYsmJq1RiZ-YiH-IJLcAZUVkVff-KBPwEmmaQGH6aC","amount":"205000000","payload":"te6cckEBAgEAhwABbQ+KfqUAAADNgG7tIEATEtAIAO87mQKicbKgHIk4pSPP4k5xhHqutqYgAB7USnesDnCcECwbgQMBAJUlk4VhgBD3JEg1TUr75iTijBghOKm/sxNDXUBl7CD6WMut0Q85x4RafwA/Es89DBXoTxuqxVFxyBbzt9Rav2HBUKl7hmkvLuKHLBCW57aK"}],"validUntil":1792481054,"network":-239}
      this.testTransaction = JSON.stringify(data)
    },
    async mockTransaction(){
     let account = window.provider.account;
      let address =  account.address
      var data  = {"messages":[{"address":"UQDXlIPZgiC7ugBqdg68ZQ4zBVGaTEfTQd1ywCDB8exACtQK","amount":1100000}],
        "valid_until":1792481054,
        "from":address,
        "network":"-239"}
      this.testTransaction = JSON.stringify(data)
    },
    async mockTransactionNFT(){
      var data = {"from":"EQD8Szz0MFehPG6rFUXHIFvO31Fq_YcFQqXuGaS8u4ocsMqN","messages":[{"address":"EQBpDpRrE3b4g01AXoyCQbnUGTpVCCgCBGyTPWvKvwDyQKqZ","amount":"20000000","payload":"te6cckEBAQEAUwAAoV/MPRQAAAAAAAAAAIAa8pB7MEQXd0ANTsHXjKHGYKozSYj6aDuuWAQYPj2IAVAD8Szz0MFehPG6rFUXHIFvO31Fq/YcFQqXuGaS8u4ocsAgKGBCo0E="}],"valid_until":1792481054,"network":"-239"}
      this.testTransaction = JSON.stringify(data)
    },
    async sendTransaction(){


      console.log('sendTransaction')
      console.log('sendTransaction内容------',this.testTransaction)
      if (this.testTransaction) {
        // eslint-disable-next-line no-undef
        const sendData = JSON.parse(this.testTransaction)
        try {
          var result = await window.provider.sendTransaction(sendData)
          this.testboc = result.boc
        } catch (error) {
          if (error.code === OKX_CONNECT_ERROR_CODES.USER_REJECTS_ERROR) {
            this.testboc = "用户拒绝交易"
          } else {
            this.testboc = "签名错误！"
          }
        }
      } else {
        alert("交易数据为空，请选则或输入交易数据。")
      }
    },

    async testTonSdk(){
      if (window.provider.connected){
        console.log('you have connected wallet');
      }else {
        console.log('APP ---connect Proof--->>>>----',this.testProofPayload);
        let connectData = {
          tonProof:this.testProofPayload,
          redirect:"tg://resolve",
          tmaReturnUrl:"tg://resolve",
        };

        if (this.isPC()) {
          if (this.isSafariOrChrome() && !window?.TelegramWebview && !window?.TelegramWebviewProxy){
            connectData.redirect = null;
          }
          console.log('Is Mobile ==>',connectData);

        }else {
          if (this.isSafariOrChrome() && !window?.TelegramWebview && !window?.TelegramWebviewProxy){
            connectData.redirect = null;
          }
          console.log('Is Mobile ==>',connectData);
        }

        if (window.provider instanceof OKXTonConnectUI) {
          if (this.testProofPayload){
            window.provider.setConnectRequestParameters({
              state: "ready",
              value: {tonProof:this.testProofPayload}
            });
          }
          await window.provider.openModal().catch(
            (error) => console.error("发生了错误：", error));
        } else if(window.provider instanceof OKXTonConnect)  {
           window.provider.connect(connectData).then((url)=>{
             console.log("Current Url ---:",url)
             if (url && this.isPC()){
               this.qrValue = url;
               this.showModal = true;
             }
           });
        }
      }
    },

    closeModal() {
      this.showModal = false;
    },

    isPC() {
      const userAgentInfo = navigator.userAgent
      console.log('current device ua is ===>',userAgentInfo)

      const Agents = ["Android", "iPhone", "SymbianOS", "Windows Phone", "iPad", "iPod"]
      let flag = true
      for (let v = 0; v < Agents.length; v++) {
        if (userAgentInfo.indexOf(Agents[v]) > 0) {
          flag = false
          break
        }
      }
      return flag
    },
    isSafariOrChrome() {
      const userAgentInfo = navigator.userAgent
      const Agents = ["Safari", "Chrome"]
      let flag = false
      for (let v = 0; v < Agents.length; v++) {
        if (userAgentInfo.indexOf(Agents[v]) > 0) {
          flag = true
          break
        }
      }
      return flag
    },

    handleReturnStrategySelect(returnStrategy) {
      console.log('handleReturnStrat', window.provider);
      selectedReturnStrategy = returnStrategy === "deeplink" ? this.deeplink : returnStrategy;

      if (this.withUI) {
        window.provider.uiOptions = {
          actionsConfiguration: {
            returnStrategy: selectedReturnStrategy,
            modals: "all"
          },
          buttonRootId: 'button-root',
        }
      }
    },

    handleReturnStrategyDeeplink(deeplink) {
      console.log(window.provider);
      selectedReturnStrategy = deeplink;

      if (this.withUI) {
        window.provider.uiOptions = {
          actionsConfiguration: {
            returnStrategy: deeplink,
            modals: "all"
          },
          buttonRootId: 'button-root',
        };
      }
    }
  },
  created() {

    for (const eventName of Object.values(OKX_TON_CONNECTION_AND_TRANSACTION_EVENT)) {
      console.log(eventName);
      window.addEventListener(eventName, (event) => {
        console.log(eventName,event.detail)
        this.currentStatus = eventName
      });
    }

    window.addEventListener("okx-ton-connect-connection-info-before-jump", (event) => {
      console.log("链接详情   :::",event.detail)
    });

  },

  mounted() {
    var scriptTag = document.createElement("script");
    scriptTag.src = "https://telegram.org/js/telegram-web-app.js";
    scriptTag.id = "tgsdk";
    document.getElementsByTagName('head')[0].appendChild(scriptTag);

    this.connectReady = true

    document.title = "OKX Connect TON";
    var TON;
    if (!this.withUI) {
      console.log('---init wallet------');

      TON = new OKXTonConnect({
        metaData:{
        name:"OKX TON Connect Demo",
        icon: "https://avatars.githubusercontent.com/u/37784886"
      }})

    } else {
      console.log('---init wallet UI--');
      TON = new OKXTonConnectUI({
        dappMetaData: {
          name: "OKX TON Connect Demo",
          icon: "https://avatars.githubusercontent.com/u/37784886",
        },
        actionsConfiguration: {
          returnStrategy: 'tg://resolve',
          tmaReturnUrl:'back',
          modals: "all",
        },
        uiPreferences: {
          theme: THEME.LIGHT
        },
        buttonRootId: 'button-root',
        language: 'en_US',
      });
    }
      window.provider = TON;
      
    window.provider.onStatusChange((walletInfo) => {
      console.log('APP ---TON.onStatusChange>>----', walletInfo);
          if (walletInfo) {
            this.walletAddress = walletInfo.account.address;
            if (this.testProofPayload) {
              var checkProofData = {
                address: this.walletAddress,
                network: walletInfo.account.chain,
                public_key:walletInfo.account.publicKey,
                proof:{
                  ... walletInfo.connectItems.tonProof.proof,
                  state_init: walletInfo.account.walletStateInit
                }
              }
              console.log(`checkProof => ${JSON.stringify(checkProofData)}`)

            } else {
              this.ProofPayloadVerify = ": no proof";
            }
            this.showModal = false;

          } else {
            this.walletAddress = "disconnected";
          }
        }, (error) => {
          this.showModal = false;
      if (error.code === OKX_CONNECT_ERROR_CODES.USER_REJECTS_ERROR){
        this.walletAddress = `用户拒绝链接: code(${error.code}) `;
      }else {
        this.walletAddress = `链接报错: code(${error.code})  ${error.message}`;
      }
        }
    )

  }
}
</script>